footer {
  background-color: #0f3460;
  padding: 100px 0;
  color: #fff;
  margin-top: 150px;
}
footer .container{
    padding: 0 7%;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}
footer .box {
     flex-basis: 37%;
}
footer h1 {
  margin-bottom: 30px;
  color: #e94560;
  font-weight: 800;
  font-style: italic;
}
footer .box p {
    text-align: left;
}
footer .img {
  background-color: #0c2a4d;
  padding: 15px 20px;
  border-radius: 5px;
}
footer .img i {
  font-size: 20px;
  margin: 5px;
}
footer .img span {
  margin-top: 5px;
}
footer p {
  font-size: 14px;
  font-weight: 300;
  opacity: 0.5;
  margin-bottom: 20px;
}
footer h2 {
  margin-bottom: 20px;
}
footer li {
  margin-bottom: 10px;
  opacity: 0.5;
}
@media only screen  and (max-width: 768px){
    footer .container {
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }
   
}